import { useEffect, useState } from 'react';
import { motion } from 'framer-motion';

interface PreloaderProps {
    onComplete: () => void;
}

const Preloader = ({ onComplete }: PreloaderProps) => {
    const [text, setText] = useState("");
    const fullText = "INITIALIZING SYSTEM...\nLOADING MODULES...\nESTABLISHING CONNECTION...\nACCESS GRANTED";

    useEffect(() => {
        let currentIndex = 0;
        const interval = setInterval(() => {
            if (currentIndex <= fullText.length) {
                setText(fullText.slice(0, currentIndex));
                currentIndex++;
            } else {
                clearInterval(interval);
                setTimeout(onComplete, 1000);
            }
        }, 50);

        return () => clearInterval(interval);
    }, [onComplete]);

    return (
        <motion.div
            className="fixed inset-0 z-[100] bg-black flex items-center justify-center font-mono text-primary text-xl md:text-2xl"
            initial={{ opacity: 1 }}
            exit={{ opacity: 0 }}
            transition={{ duration: 0.5 }}
        >
            <div className="whitespace-pre-line leading-loose">
                {text}
                <span className="animate-pulse">_</span>
            </div>
        </motion.div>
    );
};

export default Preloader;
