import { useState } from 'react';
import { motion, AnimatePresence } from 'framer-motion';
import { Menu, CircleX, Terminal } from 'lucide-react';

import GlitchText from './GlitchText';

const Navbar = () => {
    const [isOpen, setIsOpen] = useState(false);

    const links = [
        { name: 'Home', href: '#home' },
        { name: 'Projects', href: '#projects' },
        { name: 'Skills', href: '#skills' },
        { name: 'Contact', href: '#contact' },
    ];

    return (
        <nav className="fixed top-0 left-0 right-0 z-50 bg-background/90 border-b border-white/10 backdrop-blur-md">
            <div className="max-w-[1400px] mx-auto px-6 h-20 flex justify-between items-center">
                <motion.a
                    href="#home"
                    className="flex items-center gap-2 font-display text-2xl font-bold uppercase tracking-tighter text-white"
                    initial={{ opacity: 0, x: -20 }}
                    animate={{ opacity: 1, x: 0 }}
                >
                    <Terminal className="text-primary" size={24} />
                    <GlitchText text="MD_REHAN_SAQUIB" />
                </motion.a>

                {/* Desktop Menu */}
                <div className="hidden md:flex items-center space-x-12">
                    {links.map((link, index) => (
                        <motion.a
                            key={link.name}
                            href={link.href}
                            className="font-mono text-sm uppercase tracking-widest text-muted hover:text-primary transition-colors relative group"
                            initial={{ opacity: 0, y: -10 }}
                            animate={{ opacity: 1, y: 0 }}
                            transition={{ delay: index * 0.1 }}
                        >
                            {link.name}
                            <span className="absolute -bottom-1 left-0 w-0 h-0.5 bg-primary group-hover:w-full transition-all duration-300" />
                        </motion.a>
                    ))}
                </div>

                {/* Mobile Menu Button */}
                <div className="md:hidden">
                    <button onClick={() => setIsOpen(!isOpen)} className="text-white hover:text-primary transition-colors">
                        {isOpen ? <CircleX size={32} /> : <Menu size={32} />}
                    </button>
                </div>
            </div>

            {/* Mobile Menu Overlay */}
            <AnimatePresence>
                {isOpen && (
                    <motion.div
                        initial={{ opacity: 0, height: 0 }}
                        animate={{ opacity: 1, height: '100vh' }}
                        exit={{ opacity: 0, height: 0 }}
                        className="md:hidden absolute top-20 left-0 right-0 bg-background border-b border-white/10 overflow-hidden"
                    >
                        <div className="flex flex-col items-center justify-center h-full gap-8 pb-20">
                            {links.map((link) => (
                                <a
                                    key={link.name}
                                    href={link.href}
                                    onClick={() => setIsOpen(false)}
                                    className="font-display text-4xl font-bold uppercase text-white hover:text-primary transition-colors"
                                >
                                    {link.name}
                                </a>
                            ))}
                        </div>
                    </motion.div>
                )}
            </AnimatePresence>
        </nav>
    );
};

export default Navbar;
