"use client";

import { useState } from 'react';
import { AnimatePresence } from 'framer-motion';
import Navbar from '../components/Navbar';
import Hero from '../components/Hero';
import Skills from '../components/Skills';
import Projects from '../components/Projects';
import Contact from '../components/Contact';
import Footer from '../components/Footer';
import CustomCursor from '../components/CustomCursor';
import Preloader from '../components/Preloader';
import SmoothScroll from '../components/SmoothScroll';
import Terminal from '../components/Terminal';

export default function Home() {
    const [loading, setLoading] = useState(true);

    // We are skipping the noise overlay explicitly if it was causing issues, or we can keep it.
    // The original App.tsx had it. Let's include it.

    return (
        <>
            <CustomCursor />
            <Terminal />
            <AnimatePresence mode='wait'>
                {loading && <Preloader onComplete={() => setLoading(false)} />}
            </AnimatePresence>

            {!loading && (
                <div className="bg-background min-h-screen text-text font-sans selection:bg-primary selection:text-black">
                    {/* Global Grain Overlay */}
                    <div className="fixed inset-0 pointer-events-none opacity-5 z-[100] overflow-hidden">
                        {/* Note: The URL might need to be in public folder or remote. It was a remote URL in App.tsx */}
                        <div className="w-[200%] h-[200%] absolute top-[-50%] left-[-50%] bg-[url('https://upload.wikimedia.org/wikipedia/commons/7/76/Noise_overlay.png')] animate-grain" />
                    </div>

                    <Navbar />
                    <SmoothScroll>
                        <Hero />
                        <Skills />
                        <Projects />
                        <Contact />
                        <Footer />
                    </SmoothScroll>
                </div>
            )}
        </>
    );
}
