import { motion } from 'framer-motion';
import { Database, Layout, Smartphone, Settings } from 'lucide-react';

const skills = [
    {
        category: "FRONTEND_OPS",
        icon: <Layout className="text-primary" size={24} />,
        items: ["React", "Next.js", "TypeScript", "Tailwind CSS", "Framer Motion"]
    },
    {
        category: "BACKEND_SYS",
        icon: <Database className="text-primary" size={24} />,
        items: ["Node.js", "Express", "PostgreSQL", "MongoDB", "Supabase"]
    },
    {
        category: "MOBILE_DEV",
        icon: <Smartphone className="text-primary" size={24} />,
        items: ["React Native", "Expo", "iOS", "Android"]
    },
    {
        category: "DEVOPS_TOOLING",
        icon: <Settings className="text-primary" size={24} />,
        items: ["Git", "Docker", "AWS", "VS Code", "Figma"]
    }
];

const Skills = () => {
    return (
        <section id="skills" className="py-24 px-6 bg-background relative overflow-hidden">
            {/* Background Grid Accent */}
            <div className="absolute inset-0 bg-grid-white opacity-20 pointer-events-none" />

            <div className="max-w-[1400px] mx-auto relative z-10">
                <motion.div
                    initial={{ opacity: 0, y: 20 }}
                    whileInView={{ opacity: 1, y: 0 }}
                    viewport={{ once: true }}
                    className="mb-16 border-b border-white/20 pb-4"
                >
                    <h2 className="font-display text-5xl md:text-6xl font-bold uppercase tracking-tighter">
                        Technical<span className="text-primary">_Specs</span>
                    </h2>
                </motion.div>

                <div className="grid grid-cols-1 md:grid-cols-2 lg:grid-cols-4 gap-4">
                    {skills.map((skill, index) => (
                        <motion.div
                            key={skill.category}
                            initial={{ opacity: 0, scale: 0.95 }}
                            whileInView={{ opacity: 1, scale: 1 }}
                            transition={{ delay: index * 0.1 }}
                            viewport={{ once: true }}
                            className="bg-surface border border-white/10 p-6 hover:border-primary transition-colors group"
                        >
                            <div className="flex items-center gap-3 mb-6 border-b border-white/5 pb-4">
                                <div className="p-2 bg-white/5 group-hover:bg-primary/20 transition-colors">
                                    {skill.icon}
                                </div>
                                <h3 className="font-mono text-sm font-bold tracking-widest text-primary">
                                    {skill.category}
                                </h3>
                            </div>

                            <ul className="space-y-3">
                                {skill.items.map((item) => (
                                    <li key={item} className="flex items-center justify-between text-muted text-sm font-mono group-hover:text-white transition-colors">
                                        <span>{item}</span>
                                        <div className="w-1.5 h-1.5 bg-white/10 group-hover:bg-primary transition-colors" />
                                    </li>
                                ))}
                            </ul>
                        </motion.div>
                    ))}
                </div>
            </div>
        </section>
    );
};

export default Skills;
