import { motion } from 'framer-motion';
import { ArrowUpRight, Code2 } from 'lucide-react';
import DistortionImage from './DistortionImage';

const projects = [
    {
        title: "E-Commerce\nDashboard",
        description: "Real-time analytics and inventory management system with predictive styling.",
        tags: ["React", "Tailwind", "Recharts"],
        image: "https://images.unsplash.com/photo-1551288049-bebda4e38f71?auto=format&fit=crop&q=80&w=1000",
        className: "md:col-span-2 md:row-span-2",
        links: { demo: "#", github: "#" }
    },
    {
        title: "Task\nCommand",
        description: "Keyboard-first task manager for power users.",
        tags: ["Next.js", "TypeScript"],
        image: "https://images.unsplash.com/photo-1454165804606-c3d57bc86b40?auto=format&fit=crop&q=80&w=1000",
        className: "md:col-span-1 md:row-span-1",
        links: { demo: "#", github: "#" }
    },
    {
        title: "AI\nGenerator",
        description: "Stable diffusion interface.",
        tags: ["React", "Node.js"],
        image: "https://images.unsplash.com/photo-1617791160505-6f00504e35d9?auto=format&fit=crop&q=80&w=1000",
        className: "md:col-span-1 md:row-span-1",
        links: { demo: "#", github: "#" }
    }
];

const Projects = () => {
    return (
        <section id="projects" className="py-24 px-6 bg-surface">
            <div className="max-w-[1400px] mx-auto">
                <motion.div
                    initial={{ opacity: 0, x: -20 }}
                    whileInView={{ opacity: 1, x: 0 }}
                    viewport={{ once: true }}
                    className="mb-16 flex items-end gap-4"
                >
                    <h2 className="font-display text-6xl md:text-8xl font-bold uppercase leading-none">
                        Selected<br /><span className="text-primary">Works</span>
                    </h2>
                    <div className="h-4 w-4 bg-primary mb-4 animate-pulse" />
                </motion.div>

                <div className="grid grid-cols-1 md:grid-cols-3 gap-6 auto-rows-[300px]">
                    {projects.map((project, index) => (
                        <motion.div
                            key={project.title}
                            initial={{ opacity: 0, y: 20 }}
                            whileInView={{ opacity: 1, y: 0 }}
                            transition={{ delay: index * 0.1 }}
                            viewport={{ once: true }}
                            className={`group relative overflow-hidden bg-background border border-white/10 hover:border-primary/50 transition-colors ${project.className}`}
                        >
                            {/* Background Image with Overlay */}
                            <div className="absolute inset-0">
                                <DistortionImage
                                    src={project.image}
                                    alt={project.title}
                                    className="w-full h-full opacity-40 group-hover:opacity-20 group-hover:scale-105 transition-all duration-700"
                                />
                                <div className="absolute inset-0 bg-gradient-to-t from-black via-black/50 to-transparent" />
                            </div>

                            {/* Content */}
                            <div className="absolute inset-0 p-8 flex flex-col justify-end">
                                <div className="transform translate-y-4 group-hover:translate-y-0 transition-transform duration-500">
                                    <h3 className="font-display text-4xl font-bold uppercase leading-none mb-2 text-white group-hover:text-primary transition-colors whitespace-pre-line">
                                        {project.title}
                                    </h3>

                                    <div className="flex flex-wrap gap-2 mb-4 opacity-0 group-hover:opacity-100 transition-opacity duration-500 delay-100">
                                        {project.tags.map(tag => (
                                            <span key={tag} className="text-xs font-mono uppercase bg-white/10 px-2 py-1 text-primary">
                                                {tag}
                                            </span>
                                        ))}
                                    </div>

                                    <div className="flex items-center gap-4 opacity-0 group-hover:opacity-100 transition-opacity duration-500 delay-200">
                                        <a href={project.links.demo} className="flex items-center gap-1 text-sm font-bold uppercase hover:text-primary">
                                            Live <ArrowUpRight size={14} />
                                        </a>
                                        <a href={project.links.github} className="flex items-center gap-1 text-sm font-bold uppercase hover:text-primary">
                                            Code <Code2 size={14} />
                                        </a>
                                    </div>
                                </div>
                            </div>

                            {/* Decorative Corner */}
                            <div className="absolute top-0 right-0 p-4 opacity-0 group-hover:opacity-100 transition-opacity">
                                <ArrowUpRight className="text-primary w-8 h-8" />
                            </div>
                        </motion.div>
                    ))}
                </div>
            </div>
        </section>
    );
};

export default Projects;
