import { useState, useEffect, useRef } from 'react';
import { motion, AnimatePresence } from 'framer-motion';
import { Terminal as TerminalIcon, X } from 'lucide-react';
// import useSound from '../hooks/useSound';

const Terminal = () => {
    const [isOpen, setIsOpen] = useState(false);
    const [input, setInput] = useState('');
    const [history, setHistory] = useState<string[]>(["Welcome to DEV_PORT_V1 Terminal.", "Type 'help' for available commands."]);
    const inputRef = useRef<HTMLInputElement>(null);
    const bottomRef = useRef<HTMLDivElement>(null);

    // Simple beep sound (placeholder URL, use a real one or generic base64 if needed)
    // For now, we'll skip the actual audio call to avoid CORS/missing file issues unless user provides one.
    // const [playBeep] = useSound('/beep.mp3'); 

    useEffect(() => {
        const handleKeyDown = (e: KeyboardEvent) => {
            if (e.ctrlKey && e.key === 'k') {
                e.preventDefault();
                setIsOpen(prev => !prev);
            }
            if (e.key === 'Escape') {
                setIsOpen(false);
            }
        };

        window.addEventListener('keydown', handleKeyDown);
        return () => window.removeEventListener('keydown', handleKeyDown);
    }, []);

    useEffect(() => {
        if (isOpen && inputRef.current) {
            inputRef.current.focus();
        }
    }, [isOpen]);

    useEffect(() => {
        if (bottomRef.current) {
            bottomRef.current.scrollIntoView({ behavior: 'smooth' });
        }
    }, [history]);

    const handleCommand = (e: React.FormEvent) => {
        e.preventDefault();
        const cmd = input.trim().toLowerCase();
        if (!cmd) return;

        let response = "";
        switch (cmd) {
            case 'help':
                response = "Available commands: help, about, projects, contact, clear, exit";
                break;
            case 'about':
                response = "I am a Full Stack Developer building aggressive modern web experiences.";
                break;
            case 'projects':
                response = "Navigating to Projects section...";
                document.getElementById('projects')?.scrollIntoView({ behavior: 'smooth' });
                setIsOpen(false);
                break;
            case 'contact':
                response = "Navigating to Contact section...";
                document.getElementById('contact')?.scrollIntoView({ behavior: 'smooth' });
                setIsOpen(false);
                break;
            case 'clear':
                setHistory([]);
                setInput('');
                return;
            case 'exit':
                setIsOpen(false);
                setInput('');
                return;
            default:
                response = `Command not found: ${cmd}`;
        }

        setHistory(prev => [...prev, `> ${cmd}`, response]);
        setInput('');
    };

    return (
        <AnimatePresence>
            {isOpen && (
                <motion.div
                    initial={{ opacity: 0, scale: 0.95 }}
                    animate={{ opacity: 1, scale: 1 }}
                    exit={{ opacity: 0, scale: 0.95 }}
                    transition={{ duration: 0.1 }}
                    className="fixed inset-0 z-[100] flex items-center justify-center p-4 bg-black/60 backdrop-blur-sm"
                    onClick={(e) => {
                        if (e.target === e.currentTarget) setIsOpen(false);
                    }}
                >
                    <div className="w-full max-w-2xl bg-black border border-primary/50 shadow-[0_0_30px_rgba(204,255,0,0.1)] rounded-sm overflow-hidden flex flex-col h-[400px]">
                        {/* Header */}
                        <div className="bg-surface border-b border-primary/20 p-2 flex items-center justify-between">
                            <div className="flex items-center gap-2 text-primary px-2">
                                <TerminalIcon size={16} />
                                <span className="font-mono text-xs font-bold uppercase tracking-widest">Warp_Terminal_v1.0</span>
                            </div>
                            <button onClick={() => setIsOpen(false)} className="text-muted hover:text-white transition-colors">
                                <X size={16} />
                            </button>
                        </div>

                        {/* Output */}
                        <div className="flex-1 p-4 font-mono text-sm overflow-y-auto text-muted space-y-2">
                            {history.map((line, i) => (
                                <div key={i} className={line.startsWith('>') ? 'text-white font-bold' : 'text-primary/80'}>
                                    {line}
                                </div>
                            ))}
                            <div ref={bottomRef} />
                        </div>

                        {/* Input */}
                        <form onSubmit={handleCommand} className="bg-surface p-2 border-t border-primary/20 flex items-center gap-2">
                            <span className="text-primary font-bold font-mono text-sm">{'>'}</span>
                            <input
                                ref={inputRef}
                                type="text"
                                value={input}
                                onChange={(e) => setInput(e.target.value)}
                                className="flex-1 bg-transparent border-none outline-none font-mono text-sm text-white placeholder-white/20"
                                placeholder="Enter command..."
                                autoFocus
                            />
                        </form>
                    </div>
                </motion.div>
            )}
        </AnimatePresence>
    );
};

export default Terminal;
