import { motion } from 'framer-motion';
import Magnetic from './Magnetic';
import { ArrowRight, Terminal } from 'lucide-react';

const Hero = () => {
    return (
        <section id="home" className="min-h-screen flex flex-col justify-center px-6 relative overflow-hidden pt-20">
            {/* Background Elements */}
            <div className="absolute top-0 right-0 w-[500px] h-[500px] bg-primary/10 rounded-full blur-[100px] -z-10 pointer-events-none" />

            <div className="max-w-[1400px] mx-auto w-full z-10">
                <motion.div
                    initial={{ opacity: 0, y: 50 }}
                    animate={{ opacity: 1, y: 0 }}
                    transition={{ duration: 0.8, ease: "easeOut" }}
                >
                    <div className="flex items-center gap-2 mb-4 text-primary font-mono text-sm tracking-widest uppercase">
                        <Terminal size={14} />
                        <span>System Online_V2.0</span>
                    </div>
                </motion.div>

                <motion.h1
                    className="font-display font-bold text-6xl md:text-8xl lg:text-[10rem] leading-[0.9] tracking-tighter text-white mb-8 uppercase"
                    initial={{ opacity: 0, y: 100 }}
                    animate={{ opacity: 1, y: 0 }}
                    transition={{ duration: 0.8, delay: 0.2 }}
                >
                    Full Stack <br />
                    <span className="text-transparent bg-clip-text bg-gradient-to-r from-primary via-white to-primary bg-[200%_auto] animate-gradient">
                        Developer
                    </span>
                </motion.h1>

                <motion.p
                    className="text-muted text-lg md:text-2xl max-w-2xl font-mono border-l-2 border-primary pl-6 mb-12"
                    initial={{ opacity: 0, x: -50 }}
                    animate={{ opacity: 1, x: 0 }}
                    transition={{ duration: 0.8, delay: 0.4 }}
                >
                    Building high-performance digital experiences with brutal precision and clean code.
                </motion.p>

                <motion.div
                    className="flex flex-col sm:flex-row gap-6 items-center"
                    initial={{ opacity: 0, y: 50 }}
                    animate={{ opacity: 1, y: 0 }}
                    transition={{ duration: 0.8, delay: 0.6 }}
                >
                    <Magnetic>
                        <a
                            href="#projects"
                            className="group flex items-center justify-center gap-3 bg-primary text-black px-8 py-4 font-bold uppercase tracking-wider hover:bg-white transition-all duration-300 clip-path-slant"
                        >
                            View Projects
                            <ArrowRight className="group-hover:translate-x-1 transition-transform" />
                        </a>
                    </Magnetic>

                    <Magnetic strength={50}>
                        <a
                            href="#contact"
                            className="group flex items-center justify-center gap-3 border border-white/20 text-white px-8 py-4 font-bold uppercase tracking-wider hover:bg-white/10 transition-all duration-300"
                        >
                            Contact Me
                        </a>
                    </Magnetic>
                </motion.div>
            </div>

            {/* Decorative bottom bar */}
            <div className="absolute bottom-10 left-0 w-full px-6 flex justify-between text-muted font-mono text-xs uppercase tracking-widest opacity-50">
                <span>[ Location: Worldwide ]</span>
                <span>[ Status: Available ]</span>
                <span className="animate-pulse">[ Scroll_Down ]</span>
            </div>
        </section>
    );
};

export default Hero;
