
import { motion } from 'framer-motion';
import { Mail, MapPin, Send } from 'lucide-react';
import Magnetic from './Magnetic';

const Contact = () => {
    return (
        <section id="contact" className="py-24 px-6 bg-surface relative overflow-hidden">
            <div className="max-w-[1400px] mx-auto">
                <motion.div
                    initial={{ opacity: 0, scale: 0.95 }}
                    whileInView={{ opacity: 1, scale: 1 }}
                    transition={{ duration: 0.5 }}
                    viewport={{ once: true }}
                    className="grid grid-cols-1 lg:grid-cols-2 gap-12"
                >
                    {/* Left Column: Info */}
                    <div>
                        <h2 className="font-display text-6xl md:text-8xl font-bold uppercase leading-none mb-8">
                            Let's<br /><span className="text-primary">Connect</span>
                        </h2>

                        <div className="space-y-8 mb-12">
                            <div className="border border-white/10 p-6 hover:border-primary transition-colors group bg-background">
                                <Mail className="text-primary mb-4 w-8 h-8" />
                                <h3 className="font-mono text-sm uppercase text-muted mb-1">Email_Address</h3>
                                <a href="mailto:hello@example.com" className="text-2xl font-bold text-white group-hover:text-primary transition-colors">
                                    hello@example.com
                                </a>
                            </div>

                            <div className="border border-white/10 p-6 hover:border-primary transition-colors group bg-background">
                                <MapPin className="text-primary mb-4 w-8 h-8" />
                                <h3 className="font-mono text-sm uppercase text-muted mb-1">Base_Station</h3>
                                <p className="text-2xl font-bold text-white">
                                    Worldwide_Remote
                                </p>
                            </div>
                        </div>
                    </div>

                    {/* Right Column: Form */}
                    <form className="bg-background border border-white/10 p-8 lg:p-12 relative group h-fit">
                        <div className="absolute top-0 right-0 p-2">
                            <div className="w-2 h-2 bg-primary animate-pulse" />
                        </div>

                        <div className="space-y-6">
                            <div>
                                <label className="block font-mono text-xs uppercase tracking-widest text-primary mb-2">Identify_Yourself</label>
                                <input
                                    type="text"
                                    className="w-full bg-surface border-b border-white/20 px-0 py-4 text-xl focus:outline-none focus:border-primary transition-colors text-white placeholder-white/20 rounded-none"
                                    placeholder="ENTER NAME"
                                />
                            </div>
                            <div>
                                <label className="block font-mono text-xs uppercase tracking-widest text-primary mb-2">Comms_Channel</label>
                                <input
                                    type="email"
                                    className="w-full bg-surface border-b border-white/20 px-0 py-4 text-xl focus:outline-none focus:border-primary transition-colors text-white placeholder-white/20 rounded-none"
                                    placeholder="ENTER EMAIL"
                                />
                            </div>
                            <div>
                                <label className="block font-mono text-xs uppercase tracking-widest text-primary mb-2">Transmission</label>
                                <textarea
                                    rows={4}
                                    className="w-full bg-surface border-b border-white/20 px-0 py-4 text-xl focus:outline-none focus:border-primary transition-colors text-white placeholder-white/20 rounded-none resize-none"
                                    placeholder="ENTER MESSAGE"
                                />
                            </div>

                            <Magnetic strength={40}>
                                <button className="w-full bg-primary text-black font-bold uppercase tracking-widest py-6 hover:bg-white transition-colors flex items-center justify-center gap-3 group/btn mt-8">
                                    Initiate_Send
                                    <Send size={18} className="group-hover/btn:translate-x-1 transition-transform" />
                                </button>
                            </Magnetic>
                        </div>
                    </form>
                </motion.div>
            </div>
        </section>
    );
};

export default Contact;
